const TOGGLE_MODAL = "MODAL::TOGGLE"
const TOGGLE_DRAWER = "DRAWER::TOGGLE"

export const toggleModal = () => ({
  type: TOGGLE_MODAL
})

export const setDrawerOpen = (state) => ({
  type: TOGGLE_DRAWER,
  state
})

export const switchTheme = () => ({
  type: "SETTINGS::SWITCH_THEME"
})