import React from 'react'
import { withRouter, useHistory } from 'react-router'
import {BottomNavigation, BottomNavigationAction} from '@material-ui/core'
import Home from '@material-ui/icons/Home'
import Person from '@material-ui/icons/Person'
import SportsEsports from '@material-ui/icons/SportsEsports'
import Blog from '@material-ui/icons/RssFeed'
import {makeStyles} from '@material-ui/core/styles'

let main = [
  {
    id: 0,
    label: "Home",
    to: "/home",
    icon: <Home />
  },
  {
    id: 1,
    label: "Tournaments",
    to: "/tournaments",
    icon: <SportsEsports />
  },
  {
    id: 2,
    label: "Games",
    to: "/games",
    icon: <Blog />
  },
  {
    id: 3,
    label: "Account",
    to: "/account",
    icon: <Person />
  }
]

const styles = makeStyles(theme => ({
  bottomNav: {
    position: 'fixed',
    zIndex: 99,
    width: '100%',
    bottom: 0,
    left: 0,
    boxShadow: theme.shadows[1]
  }
}))

function findActiveIndex(items){
  let activeIndex = 0
  items.forEach((item, index) => {
    
    if(item.to === "/")
      return
    if(window.location.pathname.indexOf(item.to) !== -1){
      activeIndex = item.id
    }
  })
  return activeIndex;
}

function BottomNav(props){
  // let [value, setValue] = React.useState("/")
  let history = useHistory()
  const classes = styles()

  const handleChange = (event, newValue) => {
    //setValue(newValue);
    history.push(newValue)
  };

  let index = findActiveIndex(main)

  return (
    <BottomNavigation value={main[index].to} onChange={handleChange} showLabels={true} className={classes.bottomNav}>
      {main.map(item => <BottomNavigationAction key={item.label} label={item.label} value={item.to} icon={item.icon}/>)}
    </BottomNavigation>
  )
}

export default withRouter(BottomNav)